import { MotionStyle } from "framer-motion"
import { isSafari, isFramerX } from "../../utils/environment"
import { RenderTarget } from "../types/RenderEnvironment"

const enableTranslateZ = isFramerX() || isSafari()
// This might seem weird, but smallValue is specified as a string here, because
// otherwise it will end up in JS as 1e-10 and interpolated as such in translateZ
const smallValue = "0.0000000001px"
const translateZ = ` translateZ(${smallValue})`

/**
 *
 * @param style The style to modify
 * @param usingMotionStyle Param to specify how to apply the layer-backed
 * transform. When usingMotionStyle is true, it will use the `translateZ` prop,
 * otherwise it will modify the CSS `transform` prop directly
 */
export function setLayerBacked(style: MotionStyle, usingMotionStyle: boolean) {
    // This enables 3d acceleration on Chrome
    style.willChange = "transform"

    // This is pretty nice for debugging
    // style.outline = "10px solid green"

    // We only want to us the translateZ hack on the canvas
    const onCanvas = RenderTarget.current() === RenderTarget.canvas

    // On Safari we can force gpu layers (avoid dumping them) by adding a small translateZ value
    // https://github.com/WebKit/webkit/blob/master/LayoutTests/compositing/layer-creation/compositing-policy.html#L23
    if (enableTranslateZ && onCanvas) {
        if (usingMotionStyle) {
            style.translateZ = smallValue
        } else {
            if (style.transform) {
                style.transform += translateZ
            } else {
                style.transform = translateZ
            }
        }
    }
}
